<script lang="ts">import { onMount } from "svelte/internal";
;
;
import Word from "./Word.svelte";
import { fade } from "svelte/transition";
import MdStars from "svelte-icons/md/MdStars.svelte";
import Icon from "./Icon.svelte";
export let game;
let score = 0;
let typedWord = "";
let input;
let invalidInput = false;
let fallingWords = [];
game.registerWordsListener((updatedList) => {
    fallingWords = updatedList;
});
game.registerScoreListener((newScore) => {
    score = newScore;
});
onMount(() => handleBlur());
const handleBlur = () => input.focus();
const resetInput = () => (typedWord = "");
const setValidInput = () => (invalidInput = false);
const handleKeyUp = (e) => {
    setValidInput();
    if (e.key == "Enter") {
        invalidInput = !game.handle(typedWord);
        resetInput();
        return;
    }
    if (game.handle(typedWord)) {
        resetInput();
    }
};
</script>

<style>
    .playground {
        height: 100vh;
    }

    .playground.invalid {
        animation: invalid 0.2s 2 alternate backwards linear;
    }

    .words-container {
        height: 100vh;
        width: 100vw;
    }

    .bottom-bar {
        position: absolute;
        bottom: 0;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 1em;
    }

    .input {
        padding: 0.6em 0;
        background: var(--color-primary);
        text-transform: uppercase;
        text-align: center;
        font-size: 1.1em;
        border-radius: var(--radius-high);
        background: var(--color-secondary-transparent);
        transition: transform var(--transition);
    }

    .score {
        opacity: 0.9;
        padding: 0.5em;
    }

    @keyframes invalid {
        75%,
        100% {
            background-color: #db3434;
        }
    }
</style>

<div
    class="playground"
    class:invalid={invalidInput}
    transition:fade={{ duration: 400 }}>
    <div class="words-container">
        {#each fallingWords as props (props.id)}
            <Word {props} />
        {/each}
    </div>
    <div class="bottom-bar">
        <div class="score">
            <Icon>
                <MdStars />
            </Icon>
            Score:
            {score}
        </div>
        <input
            class="input"
            type="text"
            spellcheck={false}
            bind:value={typedWord}
            bind:this={input}
            on:keyup={handleKeyUp}
            on:blur={handleBlur} />
    </div>
</div>
